  direction: rtl;
  unicode-bidi:embed;

  #header {
    .branding {
      float: right;

      a.brand {
        float: right;
      }
    }

    .nav-left {
      float: right!important;
      padding: 0;
      padding-left: 20px;

      .divided-right {
        border-right: 0;
        border-left: 1px solid $transparent-white-1;
      }
    }

    .search {
      &:after {
        right: auto;
        left: 0;
      }
    }

    .nav-right {
      float: left!important;
    }

    .nav-left,
    .nav-right {
      > li {
        &.nav-profile {
          >a span>i {
            margin-left: 0;
            margin-right: 5px;
          }
        }
      }
    }
  }

  .pageheader {
    .page-bar {
      .page-breadcrumb {
        > li {
          &:first-child {
            margin-right: 0;
            margin-left: 8px;
          }
        }
      }
      .page-toolbar {
        float: left;
      }
    }
  }

  #sidebar {
    accordion {
      .panel-group {
        .panel {
          >.panel-heading {
            .panel-title {
              > a {
                > i {
                  right: auto;
                  left: 0;
                }
              }
            }
          }
        }
      }
    }
    .summary {
      .media {
        .pull-right {
          float: left!important;
        }
      }
    }
    .settings {
      .onoffswitch {
        right: auto;
        left: -10px;
      }
    }
  }

  .onoffswitch {
    &.labeled {
      .onoffswitch-inner:after {
        padding-right: 0;
        padding-left: 7px;
      }
      .onoffswitch-switch {
        right: auto;
        left: 21px;
      }
    }

    &.small {
      left: -5px;
      right: auto;

      .onoffswitch-switch {
        left: 6px;
        right: auto;
      }
    }
  }

  .onoffswitch-inner {
    display: block;
    width: 200%;
    margin-right: -100%;
    margin-left: 0;
    @include transition(margin 0.2s ease-in 0s);
  }

  .onoffswitch-switch {
    right: auto;
    left: 15px;
  }

  .onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
    margin-right: 0;
  }

  .onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
    left: 0px;
    right: auto;
  }

  .onoffswitch-inner:before,
  .onoffswitch-inner:after {
    float: right;
  }

  #navigation {
    >li{
      >a{
        >i {
          margin-right: 0;
          margin-left: 10px;

          &:last-of-type {
            right: auto;
            left: 0;
          }
        }
        .badge {
          left: auto;
          right: 25px;
        }
        .label {
          left: 10px;
          right: auto;
        }
      }
    }
    .dropdown {
      >ul {
        li {
          > a {
            padding: 8px 30px 8px 0;
            > i {
              margin-right: 0;
              margin-left: 15px;

              &:first-of-type:before {
                content: "\f0d9";
              }

              &:last-of-type {
                right: auto;
                left: 0;
              }
            }
          }
        }
      }
      &.submenu {
        >ul {
          padding-left: 0;
          padding-right: 15px;
        }
      }
    }
  }

  .form-control.underline-input {
    padding-left: 12px;
    padding-right: 0;
  }

  .dropdown-menu {
    right: 0;
    left: auto;
    float: right;
    text-align: right;

    &.with-arrow {
      &:before {
        left: auto;
        right: 12px;
      }
      &:after {
        left: auto;
        right: 13px;
      }
    }

    &.pull-right {
      &.with-arrow {
        &:before {
          left: 11px;
          right: auto;
        }
        &:after {
          left: 12px;
          right: auto;
        }
      }
    }
    &.panel .panel-footer > a > i {
      float: left!important;
      &:before {
        content: "\f104";
      }
    }

    >li>a {
      >i {
        margin-right: 0;
        margin-left: 5px;
      }
      >.label,
      >.badge {
        float: left!important;
      }
    }
  }

  .dropdown {
    &.settings {
      .color-schemes {
        padding: 5px 10px;
        margin-right: 0;
      }
    }
  }

  &.header-fixed.aside-fixed {
    #content {
      right: 250px;
      left: 0;
    }
  }

  #rightbar {
    .tab-content .tab-pane ul > li .media .media-body {
      text-align: left;
    }
    .tab-content .tab-pane ul.settings > li .form-group .control-label .onoffswitch {
      right: auto;
      left: -35px;
    }
  }

  &.rightbar-hidden {
    #rightbar {
      left: -250px;
      right: auto;
    }
  }

  &.header-fixed,
  &.aside-fixed {
    &.rightbar-show {
      #rightbar {
        left: 0;
        right: auto;
      }
      #header {
        left: 250px;
        right: 0;
      }
      #content {
        left: 250px;
      }
    }
  }

  .nav-tabs.nav-justified > li {
    float: none;
  }

  &.sidebar-sm,
  &.sidebar-xs{
     #sidebar #navigation > li.dropdown > ul {
       left: auto;
       right: 100%;
     }
    #sidebar #navigation > li li.submenu > ul {
      left: auto;
      right: 100%;
    }
  }

  &.sidebar-xs {
    #sidebar #navigation > li > a .badge {
      left: auto;
      right: 5px;
    }
  }

  &.header-fixed.sidebar-sm {
    #content {
      right: 80px;
      left: 0;
      padding-right: 0;
      padding-left: 0;
    }
  }

  &.header-fixed.sidebar-xs {
    #content {
      right: 40px;
      left: 0;
      padding-right: 0;
      padding-left: 0;
    }
  }

  &.header-static {
    #header {
      padding-right: 250px;
      padding-left: 0;

      .branding {
        position: fixed;
        right: 0;
        left: auto;
      }
    }
    #content {
      padding-right: 250px;
      padding-left: 0;
      padding-top: 0;
    }

    &.sidebar-sm {
      #header {
        padding-right: 80px;
        padding-left: 0;

        .branding {
          .brand {
            margin-left: 0;
            margin-right: 8px;
          }
        }
      }
      #content {
        padding-right: 80px;
        padding-left: 0;
      }
    }

    &.sidebar-xs {
      #header {
        padding-right: 40px;
        padding-left: 0;
      }
      #content {
        padding-right: 40px;
        padding-left: 0;
      }
    }

    &.rightbar-show {
      #rightbar {
        right: auto;
        left: 0;
      }
      #header {
        padding-left: 250px;
      }
      #content {
        padding-left: 250px;
      }
    }

  }

  &.aside-static {
    #wrap {
      #controls {
        left: auto;
        right: 0;
      }
    }
    #sidebar {
      float: right;
    }
    #content {
      padding-left: 0;
      padding-right: 250px;
    }

    &.sidebar-sm {
      #content {
        padding-right: 80px;
        padding-left: 0;
      }
    }

    &.sidebar-xs {
      #content {
        padding-right: 40px;
        padding-left: 0;
      }
    }

  }

  &.aside-static.header-static {
    #header {
      padding-right: 0;
    }
  }

}

@media only screen and (max-width: 1200px) {
  .appWrapper.rtl {
    &.rightbar-show {
      #rightbar {
        left: 0;
      }
      #header {
        left: 0!important;
        padding-left: 0!important;
      }
      #content {
        left: 0!important;
        padding-left: 0!important;
      }
    }
  }
}


@media only screen and (max-width: 992px) {
  .appWrapper.rtl {
    &.sidebar-sm,
    &:not(.sidebar-sm):not(.sidebar-xs) {
      #header {
        .branding {
          .brand {
            margin-right: 8px;
            margin-left: 0;
          }
        }
      }
    }
    &:not(.sidebar-sm):not(.sidebar-xs) {
      #header {
        .branding {
          .brand {
            margin-right: -12px;
            margin-left: 0;
          }
        }
      }
    }
    &.header-fixed.aside-fixed {
      &:not(.sidebar-sm):not(.sidebar-xs) {
        #content {
          left: 0;
          right: 80px;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .appWrapper.rtl {
    &.sidebar-xs {
      #header {

        .branding {
          float: none;
          position: relative;

          >a {
            float: none;
            background-position: 8px -1px;
          }

          .brand > span {
            display: inline-block;
          }
        }

        .search {
          left: auto;
          right: 15px;
        }

        .nav-left {
          right: auto;
          left: 0;
          padding-left: 0;

          .settings .dropdown-menu {
            right: auto;
            left: 0;

            &.with-arrow {
              &:before {
                right: auto;
                left: 11px;
              }
              &:after {
                right: auto;
                left: 12px;
              }
            }
          }
        }

        .nav-right {
           float: right!important;
           padding-left: 0;
           padding-right: 3px;

          .nav-profile {
            right: auto;
            left: 40px;

            .dropdown-menu {
              left: 0;
              right: auto;

               &.with-arrow {
                &:before {
                  right: auto;
                  left: 11px;
                }
                &:after {
                  right: auto;
                  left: 12px;
                }
              }

            }
          }

          .toggle-right-sidebar {
            right: auto;
            left: 0;
          }

          .users,
          .messages,
          .notifications {
            .dropdown-menu {
              left: auto;
              right: 0;

              &.with-arrow {
                &:before {
                  left: auto;
                  right: 11px;
                }
                &:after {
                  left: auto;
                  right: 12px;
                }
              }
            }
          }
        }
      }

      &.header-static.aside-fixed {
        #header {
          .search {
            right: 55px;
            left: auto;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .appWrapper.rtl {
    &.sidebar-xs {
      #header {
        .branding {
          text-align: right;
          padding-left: 0;
          padding-right: 10px;

          >a {
            background-position: 5px -1px;
          }

          .brand > span {
            display: none;
          }
        }

        .search {
          left: auto;
          right: 55px;
        }
      }
      &.header-static.aside-fixed {
        #header {
          .search {
            left: auto;
            right: 95px;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 360px) {
  .appWrapper.rtl {
    &.rightbar-hidden {
      #rightbar {
        right: auto;
        left: -100%;
      }
    }
  }
}

.no-rtl {
  direction:ltr;
  unicode-bidi:embed;
}